import React, { Component } from 'react'
import classNames from 'classnames'
import ComponentList from 'components/Common/ComponentList'
import Icon from 'components/Common/Icon'

export default class HistoryCard extends Component {
  state = {
    active: false,
  }

  render = () => (
    <div className={classNames('PV8 HistoryCard', {
        Active: this.state.active,
        Pointer: !this.state.active,
      })}>
      <div
        className="Card BGWhite"
        onClick={() => this.setState({ active: true })}>
        <CardHead title={this.props.title} />
        <div className="CardBodyWrapper" style={{ height: this.state.active ? 1000 : 0 }}>
          <CardBody playlist={this.props.playlist} />
        </div>
      </div>
    </div>
  )
}

const CardHead = ({ title, play, onClick }) => (
  <div className="CardHead Flex JCSpaceBetween" onClick={onClick}>
    <div className="FS4x">
      <div className="ColorGray Saira SairaLight MB8">
        Conheça a história
      </div>
      <div className="ColorPrimary Saira SairaStrong Uppercase">
        {title}
      </div>
    </div>
    <img src="/static/img/ic_play_arrow_24px.svg" />
  </div>
)

const CardBody = ({ playlist = [] }) => (
  <div className="CardBody BGLightGray">
    <ComponentList component={Sound}>
      {playlist}
    </ComponentList>
  </div>
)

const Sound = ({ title, src, time }) => (
  <div className="Flex AICenter JCSpaceBetween">
    <div>Play</div>
    <div>{title}</div>
    <div>{time}</div>
  </div>
)
